@import'colores';

$colorFondo:yellow;
$fuente:roboto;
@mixin caja($ancho, $alto) {
	width: $ancho;
	height: $alto;
}

body {
	background-color: $colorFondo;
}

h1 {
	font-family: $fuente;
	color: $colorFuente;
}

ul {
	list-style: none;
	li {
		display: flex;
		justify-content: flex-start;
		a {
			text: {
				decoration: none;
				transform: uppercase;
			}
		}
	}
}
.contenido {
	width: 400px;
	.esp & {
		width: 300px;
	}
}
.cajon {
	@include caja(200px, 100px);
	background-color: $colorFondo;
}